@import url(//fonts.googleapis.com/earlyaccess/notosanskannada.css);
body {
  padding: 0;
  font-family: sans-serif;
  margin: 0;
  overflow-x: hidden;
}

html,
body {
  width: 100%;
  height: 100%;
  font-family: Poppins, Arial;
}

#root {
  width: 100%;
  height: 100%;
}

* {
  box-sizing:border-box
}

.greeting {
  background: #222;
  text-align: center;
  min-height: 100%;
  font-family: 'Noto Sans Kannada', sans-serif;
}

.greeting-header {
  padding: 20px;
  color: white;
}

.greeting-title {
  font-size: 1.5em;
}

.greeting-intro {
  font-size: large;
}

.greeting-message-block {
  background: rgba(255, 255, 255, .7);
  padding: 15px;
}

.greeting-message-block h2 {
  display: inline-block;
  margin-right: 5px;
}

.greeting .input input, textarea {
  margin-top: 5px;
  height: 40px;
  padding: 5px 10px;
  box-sizing: border-box;
  font-size: 15px;
  border-radius: 3px;
  border: none;
}

.greeting .input textarea {
  width: 100%;
  height: 5em;
  resize: vertical;
}

.greeting-form {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  text-align: left;
  color: #444;
  box-sizing: border-box;
  padding: 20px;
}

.slideshow-container {
  position: relative;
  margin: auto;
}

.prev, .next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  color: white;
  font-weight: bold;
  font-size: 18px;
  -webkit-transition: 0.6s ease;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
}

.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.prev:hover, .next:hover {
  background-color: rgba(0,0,0,0.8);
}

.numbertext {
  color: #f2f2f2;
  font-size: 12px;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

.img-choice {
  width: 100%;
  height: 300px;
  object-fit: cover;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
}

.pick {
  color: white;
  margin-bottom: 5px;
  display: block;
}

.btn {
  padding: 10px 20px;
  background: #FF8260;
  border: none;
  border-radius: 3px;
  color: white;
  display: block;
  margin: 10px auto 0;
  cursor: pointer;
  outline: none;
  width: 100%;
  font-size: 16px;
  box-shadow: none;
}

.btn:hover {
  opacity: .8;
  -webkit-transition: opacity .5s;
  transition: opacity .5s;
}

.link {
  color: white;
  display: block;
  text-align: center;
  margin-top: 20px;
}

.container {
  background: black;
  min-height: 100%;
  overflow-x: hidden;
  display: flex;
  flex-wrap: wrap;
}

.container .loading {
  text-align: center;
  color: white;
  font-size: 25px;
  position: absolute;
  top: 300px;
  width: 100%;
}

.loading img {
  width: 100px;
  margin: 0 20px 0 0;
  vertical-align: middle;
}

.container .pic-container {
  display: flex;
  flex-wrap: wrap;
}

.container .pic-block {
  flex-grow: 1;
  margin: 2px;
  height: 200px;
}

.pic-block img {
  height: 200px;
  object-fit: cover;
  max-width: 100%;
  min-width: 100%;
  vertical-align: bottom;
  -webkit-filter: grayscale(70%);
          filter: grayscale(70%);
}

.visible {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity 1s linear;
  transition: opacity 1s linear;
}

.hidden {
  visibility: hidden;
  opacity: 0;
  -webkit-transition: visibility 0s 1s, opacity 1s linear;
  transition: visibility 0s 1s, opacity 1s linear;
}

@-webkit-keyframes dialog-scale-start {
  0% {
    opacity: .5;
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes dialog-scale-start {
  0% {
    opacity: .5;
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@-webkit-keyframes message-move-in {
  0% {
    left: -500px;
  }

  25% {
    left: -500px;
  }

  100% {
    left: 0;
  }
}

@keyframes message-move-in {
  0% {
    left: -500px;
  }

  25% {
    left: -500px;
  }

  100% {
    left: 0;
  }
}

.dialog {
  position: fixed;
  display: none;
  overflow: auto;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 5;
}

.dialog .modal-sm {
  right: 0;
  left: 0;
  margin: 10% auto 50px;
  padding: 0;
  z-index: 100;
  border-radius: 3px;
  display: inline-block;
  overflow-x: hidden;
}

.show-dialog {
  display: block;
  text-align: center;
}

.dialog .modal-sm {
  -webkit-animation: dialog-scale-start .3s ease-in-out forwards;
          animation: dialog-scale-start .3s ease-in-out forwards;
}

.dialog .image-container {
  padding: 10px;
  background: white;
  display: inline-block;
  box-shadow: 0 0 8px rgba(0, 0, 0, .3);
  vertical-align: middle;
}

.image-container .user-image {
  height: 300px;
}

.dialog .message {
  display: inline-block;
  background: rgba(255, 255, 255, .9);
  padding: 30px;
  box-sizing: border-box;
  width: 500px;
  color: #444;
  text-align: left;
  margin-left: -10px;
  z-index: -1;
  position: relative;
  -webkit-animation: message-move-in 1s ease-in-out forwards;
          animation: message-move-in 1s ease-in-out forwards;
}

.message h2 {
  margin: 0;
  font-size: 2em;
}

.message p {
  margin: 10px 0 0;
  font-weight: 500;
  font-size: 1.5em;
}

.message-link {
  position: fixed;
  bottom: 50px;
  left: 0;
  display: inline-block;
  padding: 15px;
  background: #FF8260;
  color: white;
  text-decoration: none;
  border-radius: 0 3px 3px 0;
  cursor: pointer;
  font-family: 'Noto Sans Kannada', sans-serif;
}

@media screen and (max-width: 576px) {
  .image-container .user-image {
    width: 100%;
    height: auto;
  }

  .dialog .message {
    width: 100%;
  }
}

